import { apiGet, apiPost } from "./api";
//添加
export function postxtjx(data) {
    return new Promise((resolve) => {
        apiPost('role/addroles',data).then(res => {
            resolve(res)
        })
    })
}
//获取
export function getxtjx(data) {
    return new Promise((resolve) => {
        apiGet('role/getroleslist',data).then(res => {
            resolve(res)
        })
    })
}
//删除
export function delqx(id) {
    return new Promise((resolve) => {
        apiGet(`role/delroles?id=${id}`).then(res => {
            resolve(res)
        })
    })
}
//获取单个
export function getup(id) {
    return new Promise((resolve) => {
        apiPost(`role/getrolesbyid?id=${id}`).then(res => {
            resolve(res)
        })
    })
}
