<template>
    <div style="width: 100%; height: 100%;padding: 20px;box-sizing: border-box;">
        <div class="main">
            <div style="display: flex;justify-content: space-between;">
                <div>
                    <select name="" id=""
                        style="width: 150px;height: 40px;background-color: #E9F3FB;color: #229DFF;border: none;padding: 10px;">
                        <option value="" style="background-color: white;color: black;">角色状态</option>
                    </select>
                    <el-input placeholder="请输入角色名称进行模糊搜察" v-model="search" style="width: 400px;margin-left: 20px;"
                        @input="sear({ rolename: search })">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </div>
                <el-button type="primary" @click="dialogFormVisible = true"><i class="el-icon-plus"></i>
                    添加系统角色</el-button>
                <el-dialog :visible.sync="dialogFormVisible" :title="xgid ? '编辑系统角色' : '添加系统角色'"
                    style="width: 100%;margin: 0 auto;" :lockScroll="false">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm"
                        style="width: 100%;">
                        <el-form-item label="角色名称" prop="rolename">
                            <el-input v-model="ruleForm.rolename" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="角色描述" prop="describe">
                            <el-input v-model="ruleForm.describe" style="width: 270px;"></el-input>
                        </el-form-item>
                        <el-form-item label="角色权限" prop="powers">
                            <el-tree :data="data" show-checkbox default-expand-all node-key="id" ref="tree"
                                highlight-current :default-checked-keys="checkList">
                            </el-tree>
                        </el-form-item>
                        <el-form-item label="角色状态" prop="status">
                            <el-radio label="1" v-model="ruleForm.status">启动</el-radio>
                            <el-radio label="0" v-model="ruleForm.status">停用</el-radio>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submitForm()">立即提交</el-button>
                            <el-button @click="qk">取消提交</el-button>
                        </el-form-item>
                    </el-form>
                </el-dialog>
            </div>
            <div>
                <el-table :data="tableData" @select="dan" @select-all="quan">
                    <el-table-column type="selection" width="55" align="center">
                    </el-table-column>
                    <el-table-column prop="rolename" label="角色名称" width="120" align="center">
                    </el-table-column>
                    <el-table-column prop="describe" label="角色描述" show-overflow-tooltip align="center">
                        <template slot-scope="scope">
                            {{ scope.row.describe == '' ? '--' : scope.row.describe }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="powers" label="角色权限配置" show-overflow-tooltip align="center">
                    </el-table-column>
                    <el-table-column prop="status" label="角色启用状态" show-overflow-tooltip align="center">
                        <template slot-scope="scope">
                            <div style="width: 10px;height: 10px;border-radius: 50%;float: left;
                            position: relative;top: 6px;left: 60px;" :class="scope.row.status == 1 ? 'lan' : 'bai'">
                            </div> {{ scope.row.status == 1 ?
                                '启用中' : '已停用' }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="lasttime" align="center" label="最后编辑时间" show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button size="mini" icon="el-icon-edit" @click="xghx(scope.row.id)"></el-button>
                            <el-button size="mini" type="danger" icon="el-icon-delete"
                                @click="del(scope.row)"></el-button>
                            <el-button size="mini" :type="scope.row.status == 0 ? 'primary' : 'info'"
                                @click="upstat(scope.row)" style="margin-left: 10px;"
                                :icon="scope.row.status == 0 ? 'el-icon-video-play' : 'el-icon-video-pause'">
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="fenye">
                <div>
                    <p>已选 <span> {{ num }} </span> 条</p>
                    <span><a href="" @click.prevent="alldel">批量删除</a></span>
                    <span><a href="" @click.prevent="allpi">批量启用</a>/<a href="" @click.prevent="allti">停用角色</a></span>
                </div>
                <el-pagination :page-size="10" layout="total,sizes, prev, pager, next" :page-sizes="[10, 20, 30, 40]"
                    :total="yes" @current-change="fy" @size-change="jt" :current-page.sync="currentPage">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import { getxtjx, delqx, getup, upsta, sea, upqx, postxtjx } from '../../api/qxgl'

export default {
    data() {
        return {
            data: [{
                id: 1,
                label: '首页',
            }, {
                id: 2,
                label: '企业管理'
            }, {
                id: 3,
                label: '通话权限',
                children: [{
                    id: 4,
                    label: '并发统计'
                }]
            }, {
                id: 5,
                label: '权限管理',
                children: [{
                    id: 6,
                    label: '系统角色管理'
                }, {
                    id: 7,
                    label: '系统用户管理'
                }]
            }
            ],
            checkList: [],
            currentPage: 1,
            search: '',
            tableData: [],
            arr: [],
            num: 0,
            xgid: '',
            yes: 0,
            yds: 10,
            dialogFormVisible: false,
            ruleForm: {
                rolename: '',
                describe: '',
                powers: '',
                status: '',
            },
            all: [],
            rules: {
                rolename: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                ],
                powers: [
                    { required: true, message: '请输入权限', trigger: 'blur' },
                ],
                status: [
                    { required: true, message: '请选择状态', trigger: 'change' },
                ],

            },
        }
    },
    created() {
        this.get()
    },
    methods: {
        jt(a) {
            this.yds = a
            if (this.search) {
                this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
            } else {
                this.get({ page: this.currentPage, pagelimit: this.yds })
            }
        },
        //分页
        fy(y) {
            if (this.search) {
                this.sear({ page: y, rolename: this.search, pagelimit: this.yds })
            } else {
                this.get({ page: y, pagelimit: this.yds })
            }
        },
        //数据回显
        get(data) {
            getxtjx(data).then(res => {
                this.tableData = res.data.data
                if (this.tableData) {
                    this.yes = res.data.pagecount
                    this.tableData.forEach(item => {
                        var tim = new Date(item.lasttime * 1000)
                        item.lasttime = tim.getFullYear() + '-' + (tim.getMonth() + 1) + '-' + tim.getDate() + ' ' + tim.getHours() + ':' + tim.getMinutes() + ':' + tim.getSeconds()
                        var arr = ''
                        item.powers.split(',').forEach(item => {
                            if (item == '1') {
                                arr += '首页, '
                            } else if (item == '2') {
                                arr += '企业管理, '
                            } else if (item == '3') {
                                arr += '通话统计, '
                            } else if (item == '4') {
                                arr += '并发统计, '
                            } else if (item == '5') {
                                arr += '权限管理, '
                            } else if (item == '6') {
                                arr += '系统角色管理, '
                            } else if (item == '7') {
                                arr += '系统用户管理, '
                            }
                        })
                        item.powers = arr
                    })
                }
            })
        },
        //清空各种数据
        qk() {
            this.dialogFormVisible = false
            this.ruleForm = {
                rolename: '',
                describe: '',
                powers: '',
                status: '',
            }
            this.checkList = []
            console.log(this.checkList);
            this.$refs.tree.getCheckedKeys()
            this.$refs.tree.setCheckedKeys([]);
            this.xgid = ''
        },
        //表格单选框
        dan(selection) {
            this.num = selection.length
            this.all = selection
        },
        //全选
        quan(selection) {
            this.num = selection.length
            this.all = selection
        },
        //添加和修改
        submitForm() {
            // console.log(this.checkList);
            this.checkList = this.$refs.tree.getCheckedKeys()
            console.log(this.checkList);
            this.ruleForm.powers = this.checkList.join(',')
            if (this.xgid == '') {
                postxtjx(this.ruleForm).then(res => {
                    console.log(res);
                    if (res.code == 0) {
                        this.qk()
                        if (this.search) {
                            this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                        } else {
                            this.get({ page: this.currentPage, pagelimit: this.yds })
                        }
                        this.$message({
                            showClose: true,
                            message: '提交成功'
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: '提交失败'
                        });
                    }
                })
            } else {
                var upda = {
                    id: this.xgid,
                    rolename: this.ruleForm.rolename,
                    describe: this.ruleForm.describe,
                    powers: this.checkList.join(','),
                    status: this.ruleForm.status,
                }
                upqx(upda).then(res => {
                    if (res.code == 0) {
                        this.qk()
                        if (this.search) {
                            this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                        } else {
                            this.get({ page: this.currentPage, pagelimit: this.yds })
                        }
                        this.$message({
                            showClose: true,
                            message: '修改成功'
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: '修改失败'
                        });
                    }
                })
            }
        },
        //删除
        del(row) {
            this.$confirm(`删除后，${row.rolename} 将不再具备角色相应权限`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                delqx(row.id).then(res => {
                    console.log(res);
                    if (this.search) {
                        this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                    } else {
                        this.get({ page: this.currentPage, pagelimit: this.yds })
                    }
                })
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        //修改的数据回显
        xghx(id) {
            this.xgid = id
            getup(id).then(res => {
                this.ruleForm.rolename = res.data.rolename
                this.ruleForm.describe = res.data.describe
                this.checkList = res.data.powers.split(',')
                this.ruleForm.status = res.data.status
            })
            this.dialogFormVisible = true
        },
        //停用和启用
        upstat(row) {
            if (row.status == 1) {
                this.$confirm(`确认停用 ${row.rolename} 角色?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    upsta({ id: row.id, status: 0 }).then(res => {
                        console.log(res);
                        if (this.search) {
                            this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                        } else {
                            this.get({ page: this.currentPage, pagelimit: this.yds })
                        }
                    })
                    this.$message({
                        type: 'success',
                        message: '停用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消停用'
                    });
                });
            } else {
                upsta({ id: row.id, status: 1 }).then(res => {
                    console.log(res);
                    if (this.search) {
                        this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                    } else {
                        this.get({ page: this.currentPage, pagelimit: this.yds })
                    }
                    this.$message({
                        type: 'info',
                        message: '启用成功'
                    });
                })
            }
        },
        //批量删除
        alldel() {
            if (this.all.length == 0) {
                this.$message({
                    type: 'info',
                    message: '请选择要删除的数据'
                });
            } else {
                this.$confirm(`删除后，这些将不再具备角色相应权限`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.all.forEach(item => {
                        delqx(item.id).then(res => {
                            console.log(res);
                        })
                        if (this.search) {
                            this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                        } else {
                            this.get({ page: this.currentPage, pagelimit: this.yds })
                        }
                    })
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
        //批量启用
        allpi() {
            if (this.all.length == 0) {
                this.$message({
                    type: 'info',
                    message: '请选择要启用的数据'
                });
            } else {
                this.$confirm(`确认启用`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.all.forEach(item => {
                        upsta({ id: item.id, status: 1 }).then(res => {
                            console.log(res);
                            if (this.search) {
                                this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                            } else {
                                this.get({ page: this.currentPage, pagelimit: this.yds })
                            }
                        })
                        this.get()
                    })
                    this.$message({
                        type: 'success',
                        message: '启用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用'
                    });
                });
            }
        },
        //批量停用
        allti() {
            if (this.all.length == 0) {
                this.$message({
                    type: 'info',
                    message: '请选择要停用的数据'
                });
            } else {
                this.$confirm(`确认停用`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.all.forEach(item => {
                        upsta({ id: item.id, status: 0 }).then(res => {
                            console.log(res);
                            if (this.search) {
                                this.sear({ page: this.currentPage, rolename: this.search, pagelimit: this.yds })
                            } else {
                                this.get({ page: this.currentPage, pagelimit: this.yds })
                            }
                        })
                        this.get()
                    })
                    this.$message({
                        type: 'success',
                        message: '停用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消停用'
                    });
                });
            }
        },
        //搜索
        sear(data) {
            sea(data).then(res => {
                this.tableData = res.data.data
                this.yes = res.data.pagecount
                if (this.tableData) {
                    this.yes = res.data.pagecount
                    this.tableData.forEach(item => {
                        var tim = new Date(item.lasttime * 1000)
                        item.lasttime = tim.getFullYear() + '-' + (tim.getMonth() + 1) + '-' + tim.getDate() + ' ' + tim.getHours() + ':' + tim.getMinutes() + ':' + tim.getSeconds()
                        var arr = ''
                        item.powers.split(',').forEach(item => {
                            if (item == '1') {
                                arr += '首页, '
                            } else if (item == '2') {
                                arr += '企业管理, '
                            } else if (item == '3') {
                                arr += '通话统计, '
                            } else if (item == '4') {
                                arr += '并发统计, '
                            } else if (item == '5') {
                                arr += '权限管理, '
                            } else if (item == '6') {
                                arr += '系统角色管理, '
                            } else if (item == '7') {
                                arr += '系统用户管理, '
                            }
                        })
                        item.powers = arr
                    })
                }
            })
        }

    },
}
</script>
<style scoped>
a {
    text-decoration: none;
    color: #b8b8b8;
}

.main {
    width: 100%;
    background-color: white;
    padding: 20px;
    box-sizing: border-box;
}

.fenye {
    width: 100%;
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    border: 1px solid #EBEEF5;
    padding: 10px;
    box-sizing: border-box;
}

.fenye p {
    display: inline-block;
    margin: 5px 20px;
}

.fenye p span {
    color: #E6A441;
    cursor: pointer;
}

.fenye span {
    margin: 0 10px;
    color: #D9CCD3;
    font-size: 14px;
}

.bai {
    background-color: #adadad;
}

.lan {
    background-color: #6ee982;
}
</style>