// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    //可以写if判断，提前拦截错误信息
    return response;
}, function (err) {
    return Promise.reject(err);
});
// GET请求
export function apiGet(url, params) {
    return new Promise((resolve, reject) => {
        axios({
            method: 'get',
            url: url,
            params: params
        }).then(res => {
            resolve(res.data);
        }).catch(err => {
            reject(err.data)
        })
    });
}
// POST请求
export function apiPost(url, params) {
    return new Promise((resolve, reject) => {
        axios({
            method: 'post',
            url: url,
            data: params
        }).then(res => {
            resolve(res.data);
        }).catch(err => { reject(err.data) })
    });
}